import React, {Component} from "react"
import MyNavLink from "../../Components/MyNavLink";
import {Menu} from 'antd';
import './index.css'

const {SubMenu} = Menu

const data1 = [
    ["inhtml", "HTMLJavaScript"],
    ["datatype", ""],
    ["function", ""]
]

const data_obj = [
    ["object_basic", "-"],
    ["object_prototype", "-"],
    ["object_extend", "-"],
    ["object_class", "-"]
]

const data2 = [
    ["dom", "DOM"],
    ["bom", "BOM"],
    ["ajax", "AJAX"],
    ["variable", ""],
    ["event", ""]
]

const data_std = [
    ["array", "-Array"],
    ["date", "-Date"],
    ["json", "-JSON"],
    ["regexp", "-RegExp"],
    ["string", "-String"]
]

const data3 = [
    ["transform", "ES6 "],
    ["var_let_const", "var-let-const"],
    ["destructure", ""],
    ["extend", ""],
    ["deepclone", ""],
    ["iterator", ""],
]

const data_asy = [
    ["async_basic", "-"],
    ["async_promise", "-Promise"],
    ["async_generator", "-Generator"],
    ["async_func", "-async"],
]

const data4 = [
    ["proxy", ""]
]

export default class JSLeftBar extends Component {
    render() {
        return (
            <div style={{width: 256}}>
                <Menu mode="vertical" className="leftbar-menu">
                    {
                        data1.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                    <SubMenu title="">
                        {
                            data_obj.map(item => {
                                return (
                                    <Menu.Item key={item[0]}>
                                        <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                    </Menu.Item>
                                )
                            })
                        }
                    </SubMenu>
                    {
                        data2.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                    <SubMenu title="">
                        {
                            data_std.map(item => {
                                return (
                                    <Menu.Item key={item[0]}>
                                        <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                    </Menu.Item>
                                )
                            })
                        }
                    </SubMenu>
                    {
                        data3.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                    <SubMenu title="">
                        {
                            data_asy.map(item => {
                                return (
                                    <Menu.Item key={item[0]}>
                                        <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                    </Menu.Item>
                                )
                            })
                        }
                    </SubMenu>
                    {
                        data4.map(item => {
                            return (
                                <Menu.Item key={item[0]}>
                                    <MyNavLink to={"/javascript/" + item[0]} name={item[1]}/>
                                </Menu.Item>
                            )
                        })
                    }
                </Menu>
            </div>
        );
    }
}