import React, {Component} from "react"
import MyNavLink from "../../Components/MyNavLink";
import {Menu} from 'antd';
import './index.css'

const { SubMenu } = Menu;

const data_basic = [
    ["jsx", "JSX"],
    ["component", ""],
    ["state-props-refs", "state-props-refs"],
    ["event", ""],
    ["life-cycle", ""],
    ["create-react-app", "create-react-app"],
    ["controlled", ""],
    ["component-combination", ""]
]

